.mc__team {
    background-image: url('../../assets/banner.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    padding-bottom: 0rem;
    margin: 0;
    display: flex;
    flex-direction: row;
    padding-top: 5rem;
}

.mc__team a {
    cursor: pointer;
}
.mc__team-content {
    flex: 1;
    display: flex;
    justify-content: left;
    flex-direction: column;
    padding: 3rem 6rem;
    padding-bottom: 0rem;
    background-color: white;
    margin: 3rem 10rem;
    border-radius: 5px;
    text-shadow: 100px;
    font-family: var(--font-family);
    width: 45vw;
    padding-bottom: 5rem;
}

.mc__team-content p {
    font-style: italic;
    padding-top: 1rem;
    font-size: 22px;
    margin-bottom: -3rem;
    text-align: center;
}

.mc__team-content_join p {
    font-style: normal;
    text-align: left;
    padding-top: 0rem;
    font-size: 15px;
    margin-bottom: 0rem;
}

.mc__team-content h1 {
    color: rgb(65, 65, 65);
    padding: 0.5rem 0rem;
    text-align: center;
    font-size: 40px;
}

.mc__team-content_bar {
    height: 4px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;
    align-items: center;
}

.mc__team-content_join {
    justify-content: space-between;
    margin-top: 1rem;
    padding: 1rem 1rem;
    flex: 1;
    display: flex;
    flex-direction: column;
}

.mc__team-content_join p {
    margin-top: -1rem;
}

.mc__team-content_join h1 {
    margin: 1rem 0rem;
    margin-bottom: 1rem;
    color: rgb(0, 0, 0);
    text-align: left;
    font-size: 30px;
}

.mc__team-content_join h2 {
    margin-bottom: 1rem;
}

.mc__team-content_join-gray,
.mc__team-content_join-white {
    margin: 0rem 0;
    border-radius: 0.1rem;
    padding: 0.8rem 1rem;
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.mc__team-content_join-gray {
    color: white;
    background: rgb(197, 197, 197);
}

.mc__team-content_join-gray h2,
.mc__team-content_join-white h2 {
    font-size: 18px;
}

.mc__team-content_join-gray_apply,
.mc__team-content_join-white_apply {
    text-decoration: underline;
    margin-right: 1rem;
}

@media screen and (max-width: 1700px) {
    .mc__team-content {
        margin: 3rem 5rem;
        padding: 4rem 5rem;
    }
}

@media screen and (max-width: 1300px) {
    .mc__team-content {
        padding: 3rem 4rem;
    }
}

@media screen and (max-width: 700px) {
    .mc__team-content {
        margin: 2rem 2rem;
        padding: 3rem 3rem;
    }
}


@media screen and (max-width: 414px) {
    .mc__team-content {
        margin: 1.5rem 1.5rem;
        padding: 3rem 2rem;
    }
    .mc__team-content h1{
        font-size: 25px;
        margin-top: -1.5rem;
    }
    .mc__team-content p{
        font-size: 18px;
        margin-top: -0.5rem;
        margin-bottom: -3rem;
    }
    .mc__team-content_join h1{
        margin-top: 1rem;
    }
    .mc__team-content_join p{
        font-size: 15px;
        margin-top: -0.5rem;
        margin-bottom: 0rem;
    }
    .mc__team-content_join-gray_apply,
    .mc__team-content_join-white_apply {
        margin-right: 0rem
    }

}
