.mc__header {
    display: flex;
    flex-direction: row;
    background-image: url('../../assets/mythos.png');
    margin: 0;
    padding: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;  
}

.mc__header-content {
    flex: 1;
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
}

.mc__header-content h1 {
    padding-top: 16rem;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 50px;
    line-height: 50px;
    padding-left: 2rem;
    padding-right: 2rem;
    color: white;
}

.mc__header-content h2 {
    padding-top: 26rem;
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 80px;
    line-height: 50px;
    padding-left: 2rem;
    padding-right: 2rem;
    color: white;
    text-decoration: underline;
}

.mc__header-content p {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 30px;
    color: white;
    font-style: italic;
    padding-bottom: 4rem;
    padding-left: 2rem;
    padding-right: 2rem;
}
@media screen and (max-width: 1300px) {
    .mc__header-content h1 {
        padding-top: 35rem;
        font-size: 42px;
    }
    
    .mc__header-content p {
        font-size: 22px;
    }
    
}
@media screen and (max-width: 414px) {
    .mc__header {
        background-size: cover;
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-position: left;  
    }
    .mc__header-content {
        text-align: left;
    }
    .mc__header-content h1 {
        padding-top: 4rem;
    }
    .mc__header-content h2 {
        padding-top: 16rem;
        font-size: 35px;
        text-decoration: underline;
    }
    
}

