.mc__play {
    background-image: url('../../assets/banner.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    padding-bottom: 0rem;
    margin: 0;
    display: flex;
    flex-direction: row;
}

.mc__play-content {
    flex: 1;
    display: flex;
    justify-content: left;
    flex-direction: column;
    padding: 5rem 6rem;
    padding-bottom: 0rem;
    background-color: white;
    margin: 3rem 10rem;
    border-radius: 5px;
    text-shadow: 100px;
    font-family: var(--font-family);
    width: 45vw;
}

.mc__play-content p {
    padding: 0.5rem 0rem;
    width: 38vw;
    font-size: 20px;
}


.mc__play-content_join a {
    text-decoration: underline;

}

.mc__play-content_join-link {
    text-decoration: underline;
}

.mc__play-content_join-bar,
.mc__play-content_what-bar,
.mc__play-content_community-bar,
.mc__play-content_contact-bar {
    height: 3px;
    width: 38vw;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;
}

.mc__play-content_what h1,
.mc__play-content_community h1,
.mc__play-content_contact h1 {
    margin-top: 0rem;

}

.mc__play-content_button {
    width: 38vw;
}

.mc__play-content_button a {
    padding: 0.7rem 0.7rem;
    cursor: pointer;
}
.mc__play-content button {
    color: white;
    background: rgb(161, 161, 161);
    font-family: var(--font-family);
    font-size: 18px;
    text-align: center;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
    font-weight: bold; 
    justify-content: center;
    align-items: right;
    flex: 1;
    display: flex;
    width: 13rem;
    float: right;
    margin: 1rem 1rem;
    margin-bottom: 2rem;
}

.mc__play-content_join-notice {
    background-color: rgb(240, 240, 240);
    padding: 1rem 1rem;
    width: 36vw;
    color: rgb(0, 0, 0);
    border: rgb(175, 175, 175);
    border-style: solid;
    border-width: 2px;
    margin: 2rem 1rem;

}
.mc__play-content_join-notice h2 {
    font-size: 20px;
    padding: 0rem 1rem;

}

.mc__play-content_join-notice p {
    font-size: 16px;
    padding: 0.5rem 1rem;
    padding-right: 5rem;
}

.mc__play-content_front,
.mc__play-content_olympus,
.mc__play-content_hades,
.mc__play-content_underground {
    display: flex;
    flex: 1;
    flex-direction: row;
    justify-content: right;
    margin-top: 0rem;
    margin-bottom: 3rem;

}

.mc__play-content img {
    width: 30vw;
    height: 25vw;
    border: 3px solid gray;

}

.mc__play-content_join1,
.mc__play-content_what1,
.mc__play-content_community1,
.mc__play-content_contact1 {
    flex-direction: row;
    justify-content: left;
    display: flex;
}

@media screen and (max-width: 1700px) {
    .mc__play-content {
        margin: 3rem 5rem;
        padding: 4rem 5rem;
    }
}

@media screen and (max-width: 1300px) {
    .mc__play-content {
        padding: 3rem 4rem;
        
    }
    .mc__play-content h1 {
        margin: 1rem 0rem;
    }
    .mc__play-content p {
        width: auto;
    }
    .mc__play-content_join1,
    .mc__play-content_what1,
    .mc__play-content_community1,
    .mc__play-content_contact1 {
        flex-direction: column;
}
    .mc__play-content_button {
        width: auto;
        margin: 1rem 1rem;  
    }
    .mc__play-content_join div,
    .mc__play-content_what div,
    .mc__play-content_community div,
    .mc__play-content_contact div {
        width: auto;
    }
    .mc__play-content_join-notice {
        width: auto;
    }
    .mc__play-content_join-notice p {
        padding: 0.5rem 1rem;
    }
    .mc__play-content_front,
    .mc__play-content_olympus,
    .mc__play-content_hades,
    .mc__play-content_underground {
        justify-content: center;
    }
    .mc__play-content img {
        width: 100%;
        height: 100%;
    }
    .mc__play-content_underground {
        margin-bottom: 1rem;
    }
}

@media screen and (max-width: 700px) {
    .mc__play-content {
        margin: 2rem 2rem;
        padding: 3rem 3rem;
    }
    .mc__play-content_join h1 {
        margin-top: 0rem;
    }
}


@media screen and (max-width: 414px) {
    .mc__play-content {
        margin: 1.5rem 1.5rem;
        padding: 3rem 3rem;
    
    }
    .mc__play-content p {
        font-size: 18px;
    }
    .mc__play-content img {
        display: none;
    }
    .mc__play-content_join-notice {
        margin: 1rem 0;
    
    }
    .mc__play-content_join-notice p {
        font-size: 16px;
    
    }
    .mc__play-content button {
        width: 10rem;
        line-height: 22px;
        margin-bottom: 1rem;

    }
    .mc__play-content_button a {
        padding: 0.7rem 0.7rem;
    }

    .mc__play-content h1 {
        font-size: 30px;
    }

}