.mc__footer {
    text-align: center;
}
.mc__footer-container {
    background-color:  rgb(82, 82, 82);
    align-items: center;
    padding: 1rem 2rem;
}

.mc__footer-container img {
    width: 70px;
    margin: 1rem 1rem; 
    height: 70px;
    
}

@media screen and (max-width: 414px) {
    .mc__footer-container img {
        width: 50px;
        height: 50px;
        margin: 0.5rem 0.5rem; 
        
    }
    .mc__footer-container {
        background-color:  rgb(82, 82, 82);
        align-items: center;
        padding: 0.1rem 1rem;
    }
}

