.mc__myths {
    flex: 1;
    background-color: gray;
}

.mc__myths-content {
    text-align: center;
    justify-content: center;
}

.mc__myths-content h1 {
    font-family: var(--font-family);
    font-size: 30px;
    line-height: 50px;
    color: white;
    padding: 0.5rem 1rem;
}

@media screen and (max-width: 1100px) {
    .mc__myths-content h1 {

        font-size: 25px;
        line-height: 40px;
        color: white;
        padding: 0.5rem 1rem;
    }
}

@media screen and (max-width: 900px) {
    .mc__myths-content h1 {

        font-size: 23px;
        line-height: 40px;
        color: white;
        padding: 0.5rem 1rem;
    }
}

@media screen and (max-width: 414px) {
    .mc__myths-content h1 {

        font-size: 20px;
        line-height: 28px;
        color: white;
        padding: 0.5rem 1rem;
    }
}
