.mc__navbar {
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    padding: 0rem 6rem;
    margin: 0;
    background-color: rgb(66, 65, 65);
    right: 0;
    left: 0;
}

.mc__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.mc__navbar-links_logo {
    margin-right: 4rem;
}

.mc__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.mc__navbar-links_container a {
    display: block;
    color: white;
    text-align: center;
    padding: 1.5rem 4rem;
    text-decoration: none;
}

.mc__navbar-links_container a:hover {
    background-color: #111;
}

.mc__navbar-discord {
    margin-top: 0.25rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.mc__navbar-discord img {
    width: 30px;
    height: 21px;

}

.mc__navbar-links_container p,
.mc__navbar-menu_container p {
    color: white;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;

}

.mc__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.mc__navbar-menu svg {
    cursor: pointer;
}

.mc__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background-color: rgb(97, 97, 97);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 2rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.mc__navbar-menu_container p {
    margin: 1rem 0;

}

.mc__navbar-menu_container-links-discord {
    display: none;
}

.mc__navbar-menu_container-links-discord img {
    width: 30px;
    height: 21px;
}

@media screen and (max-width: 1400px) {
    .mc__navbar {
        padding-right: 1.5rem;
        padding-top:1.5rem;
        padding-bottom: 1.5rem;
    }
    .mc__navbar-links_container {
        display: none;
    }

    .mc__navbar-menu {
        display: flex;
    }
    
}

@media screen and (max-width: 414px) {

    .mc__navbar {
        padding: 1.5rem 4rem;
    }

    .mc__navbar-links_logo {
        margin-left: -1rem;
        padding-top: 0.3rem;
    }

    .mc__navbar-menu {
        margin-right: -2rem;
    }

    .mc__navbar-menu_container {
        margin-right: -1.5rem;
        min-width: 150px;
    }
}

@media screen and (max-width: 280px) {

    .mc__navbar {
        padding: 1.5rem 4rem;
        margin-left: -1rem;
    }

    .mc__navbar-menu {
        margin: 0;
    }
    .mc__navbar-menu_container {
        min-width: 150px;
    }

    .mc__navbar-discord {
        display: none;
    }

    .mc__navbar-menu_container-links-discord {
        display: flex;
    }

}

